import { useEffect, useState } from "react";
import { useParams } from "react-router-dom";
import { Nav } from 'react-bootstrap'
import { useDispatch } from "react-redux";
import { addItem } from "../store";
import { useLike } from "../hooks/likes";
import axios from "axios";
import { useUsername } from "../hooks/username";

function Detail(props){
  useEffect(()=>{
    let a = setTimeout(()=>{setAlert(false)}, 2000)
    let data = JSON.parse(localStorage.getItem('watched'))
    data.push(찾은상품.id)
    data = new Set(data)
    localStorage.setItem('watched', JSON.stringify([...data]))
    return ()=>{
      clearTimeout(a)
    }
  },[])
  let [count, setCount] = useState(0);
  let [alert, setAlert] = useState(true);
  let [탭, 탭변경] = useState(0);
  let {id} = useParams();
  let dispatch = useDispatch();
  let 찾은상품 = props.shoes.find(x=>x.id == id);

  let [like, addLike] = useLike()

  let username = useUsername()

  return(
    <div className="container">
      <div>{username}</div>
      {
        alert == true
        ? <div className="alert alert-warning">
            2초이내 구매시 할인
          </div>
          : null
      }
      
      {count}
      <button onClick={()=>{setCount(count+1)}}>버튼</button>
      <div className="row">
        <div className="col-md-6">
          <img src={"https://codingapple1.github.io/shop/shoes"+ (찾은상품.id+1) +".jpg"} width="100%" />
        </div>
        <div className="col-md-6 mt-4">
          <h4>{like}</h4>
          <button onClick={()=>{ addLike((a) => { return a + 1 }) }}>❤</button> 
          <h4 className="pt-5">{찾은상품.title}</h4>
          <p>{찾은상품.content}</p>
          <p>{찾은상품.price}</p>
          <button className="btn btn-danger" onClick={()=>{dispatch(addItem(
            {id : 찾은상품.id, name : 찾은상품.title, count: 1}))}}>주문하기</button> 
        </div>
      </div>
      <Nav variant="tabs"  defaultActiveKey="link0">
        <Nav.Item>
          <Nav.Link eventKey="link0" onClick={()=> 탭변경(0)}>버튼0</Nav.Link>
        </Nav.Item>
        <Nav.Item>
          <Nav.Link eventKey="link1" onClick={()=> 탭변경(1)}>버튼1</Nav.Link>
        </Nav.Item>
        <Nav.Item>
          <Nav.Link eventKey="link2" onClick={()=> 탭변경(2)}>버튼2</Nav.Link>
        </Nav.Item>
      </Nav>
      <TabContent 탭={탭}/>
      
    </div> 
  )
}

function TabContent({탭}){

  let [fade, setFade] = useState('')

  useEffect(()=>{
    let a = setTimeout(()=>{ setFade('end') }, 100)
    
    return ()=>{
      clearTimeout(a)
      setFade('')
    }
  }, [탭])
  return (<div className={'start ' + fade}>
    {[<div>내용0</div>, <div>내용1</div>, <div>내용2</div> ][탭]}
  </div>)
}
export default Detail;